'use client';
import { useState } from 'react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import quotes from '@/data/quotes.json';

export default function Home() {
  const [topic, setTopic] = useState('');
  const [results, setResults] = useState<{ topic: string; quote: string }[]>([]);

  const handleSearch = () => {
    const filtered = quotes.filter((q) => q.topic.toLowerCase() === topic.toLowerCase());
    setResults(filtered.slice(0, 3)); // Limit to 3 quotes
  };

  return (
    <main className="max-w-xl mx-auto p-6">
      <h1 className="text-2xl font-bold mb-4">Quote Generator</h1>
      <div className="flex gap-2 mb-6">
        <Input placeholder="Enter topic (e.g. life, motivation)" value={topic} onChange={(e) => setTopic(e.target.value)} />
        <Button onClick={handleSearch}>Search</Button>
      </div>

      {results.length > 0 && (
        <div className="space-y-4">
          {results.map((q, index) => (
            <Card key={index}>
              <CardContent className="p-4">
                <p className="text-lg">{q.quote}</p>
                <p className="text-sm text-muted-foreground mt-2">Topic: {q.topic}</p>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </main>
  );
}
